<template>
  <div class="home">
    <v-container fluid class="pa-5">
      <v-row no-gutters >
        
        <v-card color="secundary"  class="fill-height" >
          <v-card-title> Conway's Game Of Life!</v-card-title>
          <v-card-subtitle><v-icon>mdi-arrow-left</v-icon> Settings</v-card-subtitle>
          <v-card-text>
            <v-col>
              <v-btn @click.native="init"><v-icon>mdi-arm-flex</v-icon>Load</v-btn>
              <v-btn @click.native="stop"><v-icon>mdi-pause</v-icon>Pause</v-btn>
              <v-btn @click.native="start"><v-icon>mdi-play</v-icon>Start</v-btn>
              <v-btn @click.native="background"><v-icon>mdi-theme-light-dark</v-icon></v-btn>
            </v-col>
            <v-col>
              <div >
            <canvas v-if="isLoaded" id="canvas" v-bind:width="(window_width / 1.05)" v-bind:height=" (window_height / 1.5)">
              Sorry, this demo requires a web browser which supports HTML5 canvas!
            </canvas>
            </div>
            </v-col>
          
          </v-card-text>
        </v-card>
      </v-row>
    </v-container>
  </div>
</template>

<script>
/* eslint-disable */ 
export default {
  name: 'Home',
  components: {
  },
  data: () => ({
    SIZE:20,
    OPACITY:0.5,
    FOCAL_LENGTH:3,
    FPS:1,
    renderer: "",
    cubes: [],
    cubesAlive:[],
    num_cubes:"",
    ticker: "",
    cur_white: true,
    window_width: window.innerWidth,
    window_height: window.innerHeight,
    isLoaded: true

  }),
  methods:{
    init(){     
      this.isLoaded=true;
      //apply_rules will affect this array
      this.cubesAlive = [];
      var cubesAliveRow = []
      var opacity;  

      //initialize (first draw)
      for (var i = 0; i < this.SIZE; ++i) {
          cubesAliveRow = [];
        for (var j = 0; j < 1; ++j) {
          for (var k = 0; k < this.SIZE; ++k) {
              if (Math.random() < 0.5){
                  cubesAliveRow.push(1);
                  opacity = this.OPACITY;
              }else{
                  cubesAliveRow.push(0);
                  opacity = 0;
              }
            if (i == 0 || j == 0 || k == 0 ||
                i == this.SIZE-1 || j == 9 || k == this.SIZE-1) {
              var cube = Pre3d.ShapeUtils.makeCube(0.5);
              var transform = new Pre3d.Transform();
              transform.translate(i - 5, j - 5, k - 5);
              this.cubes.push({
                shape: cube,
                color: new Pre3d.RGBA(i / 10, j / 10, k / 10,opacity),
                trans: transform});
            }
          }
          this.cubesAlive.push(cubesAliveRow);
          cubesAliveRow = [];        
        }
        this.num_cubes =  this.cubes.length;
      //console.log(this.cubesAlive);  // Default to black background.
      }
      this.ticker.start();
    },
    draw(){
      console.log("drrawing!!!");
      for (var i = 0; i < this.num_cubes; ++i) {
        //ok es sencillisimo, si hay que dibuar el cubo estas lineas se tienen que ejecutar
        //ojo eh, que esto lee array no una matriz
        var cube = this.cubes[i];
        this.renderer.fill_rgba = cube.color;
        this.renderer.transform = cube.trans;
        this.renderer.bufferShape(cube.shape);        
      }  
      if (this.cur_white) {
        this.renderer.ctx.setFillColor(1, 1, 1, 1);
      } else {
        this.renderer.ctx.setFillColor(0, 0, 0, 1);
      }
      this.renderer.drawBackground();  
      this.renderer.drawBuffer();
      this.renderer.emptyBuffer();
    },
    spin_and_draw() {
        //para que un cubo no se vea simplemente cambiaremos su color por 
        //opacidad 0
        this.nextIteration();
        this.draw();
    },
    nextIteration(){
        var cubesAlive2=[];
        var cubesAliveRow = [];
        var opacity = 0;
        this.cubes=[];
        for (var i = 0; i < this.SIZE; ++i) {
            cubesAliveRow = [];
          for (var j = 0; j < 1; ++j) {
            for (var k = 0; k < this.SIZE; ++k) {
                if (this.applyRules(i,k)){                    
                    cubesAliveRow.push(1);
                    opacity = this.OPACITY;
                }else{
                    cubesAliveRow.push(0);
                    opacity = 0;
                }
              if (i == 0 || j == 0 || k == 0 ||
                  i == this.SIZE-1 || j == 1 || k == this.SIZE-1) {
                var cube = Pre3d.ShapeUtils.makeCube(0.5);
                var transform = new Pre3d.Transform();
                transform.translate(i - 5, j - 5, k - 5);
                this.cubes.push({
                  shape: cube,
                  color: new Pre3d.RGBA(i / 10, j / 10, k / 10,opacity),
                  trans: transform});
              }
            }
            cubesAlive2.push(cubesAliveRow);
            cubesAliveRow = [];        
          }      
        }
        this.cubesAlive=cubesAlive2;
        console.log(cubesAlive2);
        
    },
    applyRules(i,k){    
        if( i == 0 || i == this.SIZE-1){
            return false;
        }
        if( k == 0 || k == this.SIZE-1){
            return false;
        }
        var sum = this.getSum_Neighboors(i,k);
        if(sum>3 || sum<2){
            return false;        
        }              
        if (this.cubesAlive[i][k]==1){
        return true
        }        
        if (!this.cubesAlive[i][k]){
            if (sum>2){
            return true
            }
        }         
        return false;
    },
    getSum_Neighboors(i,j){
        var sum = 0
        sum += this.cubesAlive[i-1][j-1]+this.cubesAlive[i-1][j]+this.cubesAlive[i-1][j+1];
        sum += this.cubesAlive[i][j-1]+this.cubesAlive[i][j+1];
        sum += this.cubesAlive[i+1][j-1]+this.cubesAlive[i+1][j]+this.cubesAlive[i+1][j+1];
        return sum;
    },
    stop(){
      this.ticker.stop();
    },
    start(){
      this.ticker.start();
    },
    background(){
      this.cur_white = (this.cur_white)? false : true;
    }

  },
  mounted(){
    console.log("mounted()!!")
    var screen_canvas = document.getElementById('canvas');
    console.log(screen_canvas);
    this.renderer = new Pre3d.Renderer(screen_canvas);
    console.log(this.renderer);
    this.renderer.camera.focal_length = this.FOCAL_LENGTH;
    DemoUtils.autoCamera(this.renderer, 0, 0, -30, 0.40, -1.06, 0, this.draw);
    this.ticker = new DemoUtils.Ticker(this.FPS, this.spin_and_draw);
    console.log(this.ticker);
  }
}
</script>